

































import VueRecaptcha from 'vue-recaptcha';
import {sendMail, initMail} from '@/core/EmailSending';

export default {
  name: 'MailSender',
  props: ['title', 'content', 'sign'],
  components: {
    VueRecaptcha,
  },
  data: () => ({
    successSnackbarVisibility: false,
    errorSnackbarVisibility: false,
  }),
  methods: {
    sendMail,
  },
  mounted(): void {
    initMail();
  },
};
